import { Component } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/menu.css';
import { Link } from "react-router-dom";

export default class Menu extends Component {
    constructor(props){
        super(props);

        this.state={
            breakfast:'',
            dinner:'',
            sweets:'',
            snacks:'',
            drinks:'',
            specials:''
        }

        this.activeComponent=this.activeComponent.bind(this);
        console.log(window.location.pathname);

    }
    activeComponent(){
        console.log(window.location.href);
        if(window.location.pathname=='/menu/breakfast'){
            this.setState({
                breakfast: 'activeComponent'
            });
        }
    }
    componentDidMount(){
        this.activeComponent();
    }
    render() {
        return (
            <div id="menu">
                <h2>Menu</h2>

                <div id="mnav" className="row">
                    <div className="inner col-4" id={this.state.breakfast}><p><Link to="/menu/breakfast">Breakfast</Link></p></div>
                    <div className="inner col-4"><p><Link to="/menu/dinner">Dinner</Link></p></div>
                    <div className="inner col-4"><p><Link to="/menu/sweets">Sweets</Link></p></div>
                </div>

                <div id="mnav2" className="row">
                    <div className="inner col-6"><p><Link to="/menu/snacks">Snacks</Link></p></div>
                    <div className="inner col-6"><p><Link to="/menu/drinks">Drinks</Link></p></div>
                </div>

                <div id="mnav3" className="row">
                    <div className="inner col-12"><p><Link to="/menu/specials">Specials</Link></p></div>
                </div>
            </div>
        );
    }
}