import { Component } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/menu.css';
import pannerImg from '../assets/images1/dinner/panner.jpg';
import fullDinnerImg from '../assets/images1/dinner/full.jpg';
import brinjalImg from '../assets/images1/dinner/brinjal.jpg';
import specDinner from '../assets/images1/dinner/spec-dinner.jpg';

export default class Dinner extends Component {
    render() {
        return (
            <div>
                <div id="top">Remove your hunger!!<br />
                    <a href="/order" id="order">Order now</a>
                </div>

                <div id="img">
                    <div id="r" class="row">
                        <p class="col-lg-4 col-sm-6 "><img src={pannerImg} /><span>Panner   Rs.120</span></p>
                        <p class="col-lg-4 col-sm-6 "><img src={fullDinnerImg} /><span>Full Dinner  Rs:225</span></p>
                        <p class="col-lg-4 col-sm-6 "><img src={brinjalImg} /><span>Brinjal   Rs:95</span></p>
                        <p class="col-lg-4 col-sm-6 "><img src={specDinner} /><span>Spec dinner Rs:275</span></p>
                    </div>
                </div><br />
            </div>
        );
    }
}