import { Component } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/menu.css';
import iceCupImg from '../assets/images1/specials/icecup.jpg';
import pizaImg from '../assets/images1/specials/piza.jpg';
import omletImg from '../assets/images1/specials/omlet.jpg';

export default class Specials extends Component {
    render() {
        return (
            <div>
                <div id="img">
                    <div id="r" class="row">
                        <p class="inner col-lg-4 col-sm-6 "><img src={iceCupImg} /><span>Ice-cup   Rs:45</span></p>
                        <p class="inner col-lg-4 col-sm-6 "><img src={pizaImg} /><span>Pizza   Rs:199</span></p>
                        <p class="inner col-lg-4 col-sm-6 "><img src={omletImg} /><span>Omlet   Rs:35</span></p>

                    </div>
                </div><br />
            </div>
        );
    }
}