import { Component } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/menu.css';
import img1 from '../assets/images1/breakfast/coffe.jpeg';
import img2 from '../assets/images1/breakfast/dosa1.jpg';
import img3 from '../assets/images1/breakfast/idli.jpg';
import img4 from '../assets/images1/breakfast/pavbhaji.jpg';
import img5 from '../assets/images1/breakfast/puri.jpg';
import img6 from '../assets/images1/breakfast/vada.jpg';

export default class Breakfast extends Component {
	render() {
		return (
			<div>
				<div id="top">Breakfast is only available from 8:00 a.m to 10:30 a.m<br />
					<a href="#" id="order">Order now</a>
				</div>

				<div id="img">
					<div id="r" className="row">
						<p className="col-lg-4 col-sm-6 "><img src={img1} /><span>Coffe   Rs:15</span></p>
						<p className="col-lg-4 col-sm-6 "><img src={img2} /><span>Dosa   Rs:45</span></p>
						<p className="col-lg-4 col-sm-6 "><img src={img3} /><span>Idli   Rs:35</span></p>
						<p className="col-lg-4 col-sm-6 "><img src={img4} /><span>Pav-Bhaji Rs:55</span></p>
						<p className="col-lg-4 col-sm-6 "><img src={img5} /><span>Puri-Bhaji Rs:60</span></p>
						<p className="col-lg-4 col-sm-6 "><img src={img6} /><span>Vada Rs.20</span></p>
					</div>
				</div><br /><br />
			</div>
		);
	}
}